@import url("https://fonts.googleapis.com/css2?family=BioRhyme&family=Caprasimo&family=Poppins:wght@600&family=Roboto+Condensed:ital@1&family=Roboto:wght@900&family=Rubik+Dirt&display=swap");
@import url("https://fonts.googleapis.com/css2?family=BioRhyme&family=Caprasimo&family=Poppins:wght@300;600&family=Roboto+Condensed:ital@1&family=Roboto:wght@900&family=Rubik+Dirt&display=swap");
@import url("https://fonts.googleapis.com/css2?family=BioRhyme&family=Caprasimo&family=Poppins:wght@300;600&family=Roboto+Condensed:ital@1&family=Roboto:wght@300;900&family=Rubik+Dirt&display=swap");
/* Dashboard.js style */
@media screen and (max-width: 1440px) {
  .mainboard {
    display: flex;
    background-color: #f8faff;
  }

  .navigation {
    width: 20%;
    padding: 20px;
  }
  .dashboard {
    width: 80%;
    padding: 5px 20px 5px 20px;
  }
  /* sidebar style */
  .nav {
    background-color: #1365e8;
    border-radius: 20px;
    padding: 20px;
    text-align: center;
  }
  .navheading {
    color: #fff;
    font-family: Poppins;
    font-size: 25px;
    margin-top: 20px;
    margin-bottom: 25px;
  }
  .navitem {
    display: flex;
    justify-content: left;
    align-items: center;
    padding: 10px;
    color: #fff;
    font-family: Poppins;
    /* font-size: 300; */
    margin-bottom: 15px;
  }
  .navlink:hover {
    font-weight: 800;
  }
  .navlink {
    margin-left: 15px;
    font-weight: normal;
  }
  .navimg {
    width: 15px;
  }
  .bottomlinks {
    color: #fff;
    font-family: Poppins;
    font-size: small;
    text-align: left;
    margin-top: 240px;
    display: grid;
  }
  .bottomlink {
    margin-left: 10px;
    margin-bottom: 15px;
  }

  /* dashboard style */
  .head {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .dashheader {
    font-family: Poppins;
    font-weight: bolder;
    font-size: 20px;
    margin: 10px;
  }
  .sideheader {
    display: flex;
    margin-right: 20px;
  }
  .search {
    display: flex;
  }
  .searchbar {
    position: absolute;
    border-radius: 6px;
    padding-left: 6px;
    font-size: 15px;
    font-family: Poppins;
    background-color: #ffff;
    outline: none;
    font-size: 10px;
    height: 25px;
    width: 180px;
  }
  .searchimg {
    position: relative;
    margin-left: 140px;
    width: 14px;
    height: 14px;
    margin-top: 4px;
  }
  .lastheader {
    display: flex;
    margin-left: 30px;
  }
  .lastheader img {
    margin-left: 20px;
  }
  .user {
    width: 22px;
    height: 22px;
    border-radius: 50%;
  }

  /* TopCard.js style */
  .cardContainer {
    height: 130px;
    display: flex;
    justify-content: space-between;
  }
  .card {
    width: 220px;
    background-color: #ffff;
    height: 90%;
    border-radius: 20px;
    padding: 15px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  }
  .imgback {
    width: 28px;
    height: 28px;
    background-color: #7fcd93;
    display: inline-block;
    border-radius: 50%;
    padding: 1px;
    padding-left: 4px;
    padding-top: 3px;
  }
  .cardimg {
    object-fit: cover;
    width: 80%;
  }
  .cardhead {
    font-size: 10px;
    font-family: Poppins;
    font-weight: bold;
    margin-top: 3px;
  }
  .stats {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .numbers {
    font-size: 20px;
    font-weight: bolder;
    margin-top: 10px;
  }
  .percentage {
    margin-top: 13px;
    font-size: 10px;
    background-color: #e9f9eb;
    color: #3cc952;
    border-radius: 20px;
    padding: 5px;
  }
  /* bargraph.js */
  .graphcontainer {
    background-color: #fff;
    height: 320px;
    border-radius: 20px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  }

  .graphhead {
    display: flex;
    justify-content: space-between;
  }
  .activities {
    font-family: Poppins;
    font-size: 13px;
    font-weight: 900;
    margin: 20px 20px 5px 20px;
  }
  .dates {
    font-size: 10px;
    margin-left: 20px;
  }
  .rightG {
    display: flex;
    margin-right: 20px;
    align-items: center;
    padding: 3px;
  }
  .rightG img {
    width: 8px;
    height: 8px;
  }
  .indicatoritem {
    display: flex;
    align-items: center;
    margin-right: 30px;
  }
  .barchart {
    margin-top: 5px;
  }
  .indicatoritem img {
    margin-right: 10px;
  }
  /* Bottomcard.js style */
  .bottomcard {
    display: flex;
    margin-top: 20px;
    height: 250px;
    justify-content: space-between;
  }
  .bottomcard {
    margin-top: 20px;
    width: 26rem;
    background-color: #fff;
    height: 15rem;
    border-radius: 20px;
    padding: 15px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    display: inline-block;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .cardtitle {
    display: flex;
    justify-content: space-between;
    padding: 5px;
  }
  .cardtitlehead {
    font-family: Poppins;
    font-weight: bolder;
  }
  .carddate {
    font-family: Poppins;
    font-size: 11px;
  }
  .piechart {
    margin-bottom: 20px;
  }
  .graph {
    display: flex;
    height: 85%;
  }
  .catagory {
    margin-top: 1.5rem;
    margin-left: 5rem;
  }
  .categoryitem {
    margin-bottom: 10px;
    display: grid;
  }
  .cattitle {
    font-family: Poppins;
    font-size: 11px;
    font-weight: bolder;
  }
  .catdescription {
    font-size: 10px;
    margin-left: 17px;
  }
  .dotplustitle {
    display: flex;
    align-items: center;
  }
  .dotplustitle img {
    margin-right: 10px;
    width: 8px;
    height: 8px;
  }
  .adduser {
    width: 30px;
    position: static;

    float: right;
  }
  .adduser:hover {
    cursor: pointer;
  }

  .floating-form {
    padding: 20px;
    height: 400px;
    border-radius: 20px;
  }
  .userform {
    background-color: #ffffff;
    display: inline;
    position: fixed;
    top: 50%;
    left: 50%;
    border-radius: 20px;
    transform: translate(-50%, -50%);
    z-index: 9999;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  }
  .bottomcardsright {
    background-color: #fff;
    border-radius: 20px;
    padding: 15px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    float: right;
    height: 15rem;
    width: 26rem;
    position: relative;
    margin-top: 20px;
    margin-right: 20px;
  }
  .bottomcardcomponent {
    float: left;
  }

  .lowercomponent {
    background-color: #ffffff;
    z-index: 1;
    width: 400px;
    height: 400px;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    margin-right: 200px;
    border-radius: 20px;
  }
  .userformhead {
    font-family: Poppins;
    font-size: 13px;
    font-weight: bolder;
  }

  .categories {
    display: flex;
    justify-content: space-around;
    margin-top: 30px;
    font-family: Poppins;
    font-size: 12px;
    font-weight: bold;
    margin-bottom: 5px;
    cursor: pointer;
  }
  .basic:hover {
    font-size: 15px;
  }
  .social:hover {
    font-size: 15px;
  }
  .userinputs {
    margin-top: 10px;
    height: 230px;
  }
  .basic-inputs label,
  .social-inputs label {
    font-size: 13px;
  }
  .inputfield {
    margin-top: 5px;
    width: 100%;
    height: 40px;
    border-radius: 10px;
    padding: 5px;
    border-width: thin;
    outline: none;
  }
  .useraddbtn {
    background-color: #3f84f8;
    color: #fff;
    width: 70px;
    height: 30px;
    border-radius: 6px;
    padding: 3px;
    margin-top: 10px;
    margin-left: 250px;
  }
  .username {
    margin: 20px 0px 9px 40px;
    font-family: Poppins;
    font-weight: 600;
  }
  .info {
    width: 100%;
    height: 70%;
    margin-top: 10px;
    display: flex;
    padding: 10px;
    font-size: 13px;
    justify-content: left;
    text-align: left;
  }
  .basicdetails {
    width: 50%;
  }
  .contacts {
    margin: 30px;
    text-decoration: underline;
  }
  .linksdetails {
    width: 50%;
  }
  .sociallink {
    margin: 30px;
    text-decoration: underline;
  }
}
/* for device of max-width == 800px */

@media screen and(min-width:600px) and (max-width: 890px) {
  .nav {
    padding: 10px;
  }
  .navimg {
    width: 10px;
  }
  .navlink {
    margin-left: 6px;
  }
  .navitem {
    font-size: 13px;
  }
  .cardContainer {
    height: 100px;
  }
  .card {
    width: 150px;
    padding: 10px;
  }
  .imgback {
    width: 20px;
    height: 20px;
  }
  .cardhead {
    font-size: 10px;

    margin-top: 1px;
  }
  .numbers {
    font-size: 20px;

    margin-top: 0px;
  }
  .percentage {
    margin-top: 7px;
  }
  .barchart {
    width: 600px;
  }
  .cardtitlehead {
    font-size: 12px;
  }
  .bottomcard {
    padding: 5px;
  }
  .catagory {
    margin-left: 1rem;
  }
  .bottomcardsright {
    height: 200px;
    width: 300px;
  }
  .contacts {
    margin: 15px;
  }
  .sociallink {
    margin: 15px;
  }
  .username {
    margin: 20px 0px 9px 20px;
  }
}
